.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.selectRow:hover{
  cursor: pointer;
  box-shadow: inset 0px 0px 5px 0px #000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card__item{
  padding: 15px;
  width: 200px;
  text-align: left;
  border-bottom: 1px solid black;
}
.card__item:last-child{
  border-bottom: none;
}
.header {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
  padding-top: 10px;
}
.headerInner {
  width: 90%;
  max-width: 1000px;
  display: flex;
  justify-content: space-between;
}
.input {
  padding-bottom: 15px!important;
}
.signIn {
  padding-top: 10px!important;
}
.sign__form{
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}